@import "../common-imports";
.card-side-bar{
    content: '';
    height: 100%;
    width: 10rem;
    top: 0;
    left: 0;
    .side-bar{
        p{
            writing-mode: vertical-rl;
            transform: rotate(180deg);
            letter-spacing: 0.4rem;
        }
    } 
    &__top-arrow{
        i{
            font-size: 2.8rem;
            display: block;
            transform: rotate(90deg);
        }
    }
    &__bottom-arrow{
        i{
            font-size: 2.8rem;
            display: block;
            transform: rotate(90deg);
        }
    }
}
 
 
